.skills {
	max-width: 600px;
	margin: 0 auto;
}

.skills-list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-right: -10px;
	margin-bottom: -10px;
	color: #eee;
}

.skills-list-item {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background-color: rgba(170, 170, 170, 0.6);
	padding: 5px 10px;
	border-radius: 5px;
	min-width: 100px;
	-webkit-transition: all ease 0.3s;
	-o-transition: all ease 0.3s;
	transition: all ease 0.3s;
	margin-right: 10px;
	margin-bottom: 10px;
	-webkit-box-shadow: 2px 2px 20px rgba(180, 180, 180, 0.3);
	        box-shadow: 2px 2px 20px rgba(180, 180, 180, 0.3);
	border: 2px solid rgba(180, 180, 180, 0.3);
}

.skills-list-item__title {
	font-size: 1.2em;
	margin-right: 10px;
}

.skills-list-item:hover {
	cursor: pointer;
	background-color: #ccc;
	color: #000;
	-webkit-transition: all ease 0.3s;
	-o-transition: all ease 0.3s;
	transition: all ease 0.3s;
}

.skills-list-item:hover .skills-list-item__icon {
	opacity: 1;
}

.skills-list-item:hover .skills-list-item__title {
	opacity: 1;
}

.skills-list-item__icon {
	-webkit-filter: grayscale(50%);
	        filter: grayscale(50%);
	-webkit-transition: all ease 0.3s;
	-o-transition: all ease 0.3s;
	transition: all ease 0.3s;
	width: 40px;
	height: 40px;
	background-repeat: no-repeat;
	background-size: cover;
}



