.portfolio {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.portfolio__slider {
	max-width: 800px;
	width: 100%;
}

.portfolio-element {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.portfolio-element__title {
	font-size: 1.5em;
	margin-bottom: 20px;
}

.portfolio-element:hover {
	cursor: pointer;
}

.portfolio-element__image {
	position: relative;
	max-width: 70%;
}

.portfolio-element__image img {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	pointer-events: none;
	max-width: 100%;
}

.slick-prev {
	z-index: 1000;
	left: 0px;
}

.slick-next {
	z-index: 1000;
	right: 0px;
}



