.section {
	width: 100%;
	background: #67b26f;
	background: -webkit-gradient(linear, right top, left top, from(#4ca2cd), to(#67b26f));
	background: -webkit-linear-gradient(right, #4ca2cd, #67b26f);
	background: -o-linear-gradient(right, #4ca2cd, #67b26f);
	background: linear-gradient(to left, #4ca2cd, #67b26f);
}

.section:nth-child(2n) {
	background: #3494e6;
	background: -webkit-gradient(linear, right top, left top, from(#ec6ead), to(#3494e6));
	background: -webkit-linear-gradient(right, #ec6ead, #3494e6);
	background: -o-linear-gradient(right, #ec6ead, #3494e6);
	background: linear-gradient(to left, #ec6ead, #3494e6);
}

.section__content {
	margin: 0 auto;
	max-width: 1200px;
	padding: 100px 20px;
}



